.toast-message {
    background-color: #f95959;
    color: white;
    text-align: center;
    border-radius: 5px;
    padding: 10px 1em 10px 1em;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
    position: absolute;
    top: 3%;
    right: 50%;
    transform: translate(50%, -10%);
    margin: 50px auto;
    animation: error 0.2s ease forwards;
}

@keyframes error {
    0% {
        opacity: 0;
        transform: translateY(50%, -10%);
    }
    100% {
        opacity: 1;
        transform: translate(50%, 0);
    }
}
